@import "src/variables/variables";


.goodsAssembly {
  margin-top: 40px;
  background: rgba(148, 148, 148, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 90px;
  margin-right: 20px;
  padding: 0 20px;
  .title {
    align-self: flex-start;
    height: 40px;
    line-height: 40px;
    padding-left: 40px;
    padding-top: 10px;
    font-size: 20px;
    font-weight: 400;

  }
  .list {
    width: 100%;
    align-self: flex-start;
    display: flex;
    list-style: none;
    justify-content: space-around;
    height: 80px;

    .item {
      display: flex;
      align-items: center;
      margin-left: 40px;
      flex-grow: 1;
      width:130px;
      cursor: pointer;
      position: relative;
      }
      svg {

        fill:   $orangeBackgroundColor;


      }
      .name {
        color: black;
        transition: .1s ease;
        margin-left: 14px;
        &:hover {
          color: $orangeBackgroundColor;
        }
      }


  }
}